import { Component, OnInit } from '@angular/core';
import { Settings, AppSettings } from '../../app.settings';
import { AppService } from '../../app.service';
import { Property, Pagination } from '../../app.models';

import { Subscription } from 'rxjs';
import { MediaChange, MediaObserver } from '@angular/flex-layout';
import { debounceTime, distinctUntilChanged, map, distinct } from 'rxjs/operators';
import { GlobalState } from 'src/app/store/states/global.state';
import { PropertyLoadAction, PropertyFilteringAction } from '../properties/store/property.actions';
import { Store, select } from '@ngrx/store';
import {selectAllPropertySearch } from '../properties/store/search/property.search.selectors';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  watcher: Subscription;
  activeMediaQuery = '';

  public slides = [];
  public properties: Property[] = [];
  public viewType = 'grid';
  public viewCol = 25;
  public count = 8;
  public sort: string;
  public searchFields: any;
  public removedSearchField: string;
  public pagination: Pagination = new Pagination(1, 8, null, 2, 0, 0);
  public message: string;
  public featuredProperties: Property[];
  public showResult = false;
  public desc = ['An unprecedented concept set to transform Riverside Drive, Riverside Square is a mixed use marvel that brings together shopping, working, living and leisure.', 'An unprecedented concept set to transform Riverside Drive, Riverside Square is a mixed use marvel that brings together shopping, working, living and leisure.', 'An unprecedented concept set to transform Riverside Drive, Riverside Square is a mixed use marvel that brings together shopping, working, living and leisure.'];
  public titles = ['Milimani', 'Cairo Heights', 'El capoor', 'Serene Gardens'];
  public settings: Settings;

  public bgImages = [
    'https://images.pexels.com/photos/1396122/pexels-photo-1396122.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260',
    'https://images.pexels.com/photos/1643384/pexels-photo-1643384.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940',
    'https://images.pexels.com/photos/813692/pexels-photo-813692.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940',
    'https://images.pexels.com/photos/106399/pexels-photo-106399.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940'
   ];

   public filteredData: any = {};
  constructor(public appSettings: AppSettings,
              public appService: AppService,
              public mediaObserver: MediaObserver,

              private store: Store<GlobalState>) {

    this.settings = this.appSettings.settings;
    // tslint:disable-next-line: deprecation
    this.watcher = mediaObserver.media$.subscribe((change: MediaChange) => {
      // console.log(change)
      if (change.mqAlias == 'xs') {
        this.viewCol = 100;
      }
      else if (change.mqAlias == 'sm'){
        this.viewCol = 50;
      }
      else if (change.mqAlias == 'md'){
        this.viewCol = 33.3;
      }
      else{
        this.viewCol = 25;
      }
    });

  }

  ngOnInit() {
    this.getSlides();
    this.getProperties();
    // if (this.mediaObserver.isActive('xs')) {
    //    console.log('mobile version -XS')
    // }
    this.getFeaturedProperties();
    this.store.pipe(select(selectAllPropertySearch)).pipe(distinct(property => property)).subscribe(data => {

if (this.properties && this.properties.length > 0){
        this.settings.loadMore.page++;
        this.pagination.page = this.settings.loadMore.page;
      }

const result = this.filterData(data);
if (data.length === 0){
        this.properties.length = 0;
        this.pagination = new Pagination(1, this.count, null, 2, 0, 0);
        this.message = 'No Results Found';
        return false;
      }
if (this.properties && this.properties.length > 0){
        this.properties = this.properties.concat(result.data);
      }
      else{
        this.properties = result.data;
      }
this.pagination = result.pagination;
this.message = null;

if (this.properties.length == this.pagination.total){
        this.settings.loadMore.complete = true;
        this.settings.loadMore.result = this.properties.length;
      }
      else{
        this.settings.loadMore.complete = false;
      }

    });

    this.loadProperties({page: 1, limit: 10});

  }

  ngDoCheck(){
    if (this.settings.loadMore.load){
      this.settings.loadMore.load = false;
      this.getProperties();
    }
  }

  ngOnDestroy(){
    this.resetLoadMore();
    this.watcher.unsubscribe();
  }

  public getSlides(){
    this.appService.getHomeCarouselSlides().subscribe(res => {
      this.slides = res;
    });
  }

  public getProperties(){
    if (this.searchFields) {
      this.appService.filterProperty(this.searchFields, this.sort, this.pagination.page, this.pagination.perPage);
    }
  }

  public resetLoadMore(){
    this.settings.loadMore.complete = false;
    this.settings.loadMore.start = false;
    this.settings.loadMore.page = 1;
    this.pagination = new Pagination(1, this.count, null, null, this.pagination.total, this.pagination.totalPages);

  }

  public filterData(data){
    return this.appService.paginator(data,  this.pagination.page, this.pagination.perPage);
  }

  public searchClicked(){
    this.properties.length = 0;
    this.getProperties();
  }

  public clearChanged(event){
    this.showResult = false;
  }

  public searchChanged(event){
    event.valueChanges.subscribe(() => {
      this.showResult = true;
      this.resetLoadMore();
      this.searchFields = event.value;
      setTimeout(() => {
        this.removedSearchField = null;
      });
      if (!this.settings.searchOnBtnClick){
        if (this.properties){
          this.properties.length = 0;
        }

      }
    });
    event.valueChanges.pipe(debounceTime(500), distinctUntilChanged()).subscribe(() => {
      if (!this.settings.searchOnBtnClick){
        this.getProperties();
      }
    });
  }
  public removeSearchField(field){
    this.message = null;
    this.removedSearchField = field;
  }

  public loadProperties(data){
    this.store.dispatch(new PropertyFilteringAction(data));
  }

  public changeCount(count){
    this.count = count;
    this.resetLoadMore();
    this.properties.length = 0;
    this.getProperties();

  }

  public changeSorting(sort){
    this.sort = sort;
    this.resetLoadMore();
    this.properties.length = 0;
    this.getProperties();
  }
  public changeViewType(obj){
    this.viewType = obj.viewType;
    this.viewCol = obj.viewCol;
  }

  public getFeaturedProperties(){
    this.appService.getFeaturedProperties().subscribe(properties => {
      this.featuredProperties = properties;
    });
  }

}
